<i18n>
{
  "en": {
    "Store": "Store",
    "Nearest": "Nearest",
    "WalMart": "WalMart",
    "address": "Zhongshan avenue tangxia",
    "Charger": "Charger",
    "Borrow": "Borrow {count}",
    "Repay": "Repay {count}",
    "unit": "{count} km",
    "Empty": "Empty"
  },
  "zh": {
    "Store": "门店分类",
    "Nearest": "离我最近",
    "WalMart": "沃尔玛",
    "address": "中山大道棠下",
    "Charger": "提供座充",
    "Borrow": "可借{count}台",
    "Repay": "可还{count}台",
    "unit": "{count} 千米",
    "Empty": "空空如也"
  }
}
</i18n>
<template>
  <div class="store">
    <head-top :title='$t("Store")' :go-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <!-- <store-category></store-category> -->
      <div class="store-list" v-if="list.data&&list.data.length!==0">
        <scroll ref="scroll"
                :data="list.data"
                :pullDownRefresh="true"
                :pullUpLoad="true"
                @pullingDown="onPullingDown"
                @pullingUp="onPullingUp">
          <ul>
            <li v-for="(item, index) in list.data"
                :key="index"
                @click="storeDetails(item)">
              <!-- <div class="label">
                <span>{{ $t("Nearest") }}</span>
              </div> -->
              <div class="list-left">
                <span>
                  <!-- <img :src="item.logo || require('../../assets/global_images/details.png')" /> -->
                  <img src="../../assets/global_images/details.png" />
                </span>
              </div>
              <div class="list-right">
                <span>
                  <strong>{{item.name}}</strong>
                  <b>{{ $t("unit",{count: item.distance / 1000}) }}</b>
                </span>
                <span>
                  <img src="../../assets/store_images/time.png" alt="">
                  <strong>{{item.openTime}}</strong>
                </span>
                <span>
                  <img src="../../assets/store_images/add.png" alt="">
                  <strong>{{item.address}}</strong>
                </span>
                <span>
                  <div class="charger" v-if="item.type === 2 || item.type === 3">
                    <img src="../../assets/store_images/charger-default.png" alt="">
                    <strong>{{ $t("Charger") }}</strong>
                  </div>
                  <div class="power" v-if="item.type === 1 || item.type === 3">
                    <div class="interval" v-if="item.type === 2 || item.type === 3"></div>
                    <img src="../../assets/store_images/baby.png" alt="">
                    <strong>{{ $t("Borrow",{count: item.fullyCount}) }} {{ $t("Repay",{count: item.vacancyCount}) }}</strong>
                  </div>
                </span>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
      <div class="empty" v-else>
        <img src="../../assets/wallet_images/nothing.png" alt="">
        <span>{{ $t('Empty') }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import headTop from '../../components/Header/index'
import storeCategory from '../../components/StoreCategory/index'
export default {
  name: 'store',
  data () {
    return {
      list: {
        // 'device_type': 1,
        // 'page': 1,
        // 'count': 10,
        // 'total_page': 1,
        // 'data': [
        //   {
        //     'address': '广东省广州市天河区建中路3号',
        //     'lng': 113.367631,
        //     'distance': 0,
        //     'consumption': 888,
        //     'type': 3,
        //     'fullyCount': 55,
        //     'vacancyCount': 84,
        //     'merchantId': 114,
        //     'total_page': 1,
        //     'name': '亓行',
        //     'logo': '',
        //     'openTime': '09:00-18:00',
        //     'lat': 23.130691
        //   }
        // ]
      },
      page: 1
    }
  },

  components: {
    headTop,
    storeCategory
  },

  computed: {
    ...mapGetters([
      'position'
    ])
  },

  mounted () {
    console.log(this.position)
    this.dataInteractionResponse(res => {
      switch (res.msgType) {
        // 查询当前定位周边的门店分类返回
        case '0x5282':
          if (res.result && res.data) {
            if (this.page === 1) {
              this.list = res.data
            } else {
              res.data.data.map(i => {
                this.list.data.push(i)
              })
            }
          }
          break
      }
    })
    // 设备类型、区域、页码
    this.updatePage(1, '', 1)
  },

  methods: {
    goBackHander () {
      // console.log(123)
    },

    // 下拉刷新
    onPullingDown () {
      this.page = 1
      // 设备类型、区域、页码
      this.updatePage(1, '', 1)
    },

    // 上拉加载
    onPullingUp () {
      if (this.list.total_page > this.page) {
        // 设备类型、区域、页码
        this.updatePage(1, '', ++this.page)
      } else {
        this.$refs.scroll.forceUpdate()
      }
    },

    // 更新门店数据
    updatePage (deviceType, region, page) {
      // 查询当前定位周边的门店分类请求
      this.dataInteractionRequest({
        msgType: '0x5281',
        device_type: deviceType,
        region: region,
        lng: this.position.lng,
        lat: this.position.lat,
        page: page,
        count: 10
      })
      if (this.ISDEBUG) {
        let str = `{"msgType":"0x5282","result":true,"data":{"device_type":1,"page":${page},"count":10,"total_page":10,"data":[{"address":"广东省广州市天河区建中路3号","lng":113.367631,"distance":0,"consumption":888,"type":1,"fullyCount":55,"vacancyCount":84,"merchantId":114,"total_page":1,"name":"亓行","logo":"","openTime":"09:00-18:00","lat":23.130691}]}}`
        window.dataInteractionResponse(str)
      }
    },

    // 跳转详情
    storeDetails (item) {
      this.$router.push({
        name: 'details',
        params: {
          data: item
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.store{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .store-list{
      position: absolute;
      left: 0;
      // top: 80px;
      top: 0px;
      right: 0;
      bottom: 0;
      overflow: auto;
      ul{
        width: 100%;
        padding-top: 20px;
        li{
          .wh(720px, 320px);
          background: url('../../assets/store_images/bg.png') 0 0 no-repeat;
          background-size: cover;
          margin: 0 auto 20px auto;
          position: relative;
          .label{
            .wh(120px, 55px);
            position: absolute;
            left: 6%;
            top: -2px;
            font-size: 26px;
            color: #405C72;
            background: url('../../assets/store_images/label.png') 0 0 no-repeat;
            background-size: cover;
            span{
              width: 110%;
              display: block;
              .zoom-font(0.8);
              position: absolute;
              left: 16px;
              top: 22%;
            }
          }
          .list-left{
            .wh-f(32%, 100%);
            position: relative;
            span{
              .wh(152px, 152px);
              .f-center;
              background: #CCCCCC;
              border-radius: 20px;
              img{
                .wh(100%, 100%);
                position: absolute;
                left: 0;
                top: 0;
                border-radius: 20px;
              }
            }
          }
          .list-right{
            .wh-f(68%, 100%);
            font-size: 26px;
            text-align: left;
            padding: 0 50px 0 5px;
            span:nth-child(1) {
              .wh-l(100%, 110px);
              display: block;
              strong{
                font-size: 30px;
                font-weight: bold !important;
              }
              b{
                float: right;
                color: #A9A9A9;
              }
            }
            span:nth-child(2) {
              .wh-l(100%, 52px);
              display: block;
              color: #A9A9A9;
              font-size: 30px;
              img{
                height: 30px;
                margin-right: 12px;
                position: relative;
                top: 5px;
              }
            }
            span:nth-child(3) {
              .wh-l(100%, 52px);
              display: block;
              color: #A9A9A9;
              font-size: 26px;
              img{
                height: 30px;
                margin-right: 24px;
                position: relative;
                top: 8px;
                left: 4px;
                float: left;
              }
              strong{
                width: 90%;
                display: block;
                .text-over;
                float: left;
              }
            }
            span:nth-child(4) {
              .wh-l(110%, 100px);
              display: block;
              color: #626262;
              font-size: 24px;
              .charger{
                .wh-f(36%, 100%);
                img{
                  height: 38px;
                  margin-right: 12px;
                  position: relative;
                  top: 10px;
                  left: 4px;
                }
              }
              .power{
                .wh-f(64%, 100%);
                position: relative;
                .interval{
                  .wh-f(2px, 30px);
                  background: #EEEEEE;
                  margin: 35px 16px 0 6px;
                }
                img{
                  height: 38px;
                  margin-right: 12px;
                  position: relative;
                  top: 10px;
                  left: 4px;
                }
              }
            }
          }
        }
      }
    }
    .empty{
      img{
        height: 190px;
        margin: 260px auto 0 auto;
      }
      span{
        display: block;
        font-size: 30px;
      }
      a{
        font-style: italic;
        font-size: 30px;
        color: #50a4ff;
        text-decoration: underline;
        display: block;
        margin-top: 90px;
        &:active{
          background: none;
        }
      }
    }
  }
}
</style>



